import request from '@/utils/request'

// 商品数据导入
export const getGoodList = (data) => {
  return request.get('/api/vm-service/skuClass/search', {
    params: data
  })
}
// 商品类型搜索
export const searchGoods = (data) => {
  return request.get('/api/vm-service/skuClass/search', {
    params: data
  })
}

// 商品数据删除
export const delGoods = (data) => {
  return request.delete(`/api/vm-service/skuClass/${data}`)
}

// 新增商品类型
export const addGoods = (data) => {
  return request.post('/api/vm-service/skuClass', data)
}

// 修改商品类型
export const editGoods = (id, name) => {
  return request.put(`/api/vm-service/skuClass/${id}`, name)
}

// 获得商品数据
export const getGoodItem = (data) => {
  return request.get('/api/vm-service/sku/search', {
    params: data
  })
}

// 新增商品
export const addGoodsMg = (data) => {
  return request.post('/api/vm-service/sku', data)
}

// 修改商品
export const editGoodsItem = (data, id) => {
  return request.put(`/api/vm-service/sku/${id}`, data)
}
// 搜索商品
export const searchGoodsItem = data => {
  return request.get('/api/vm-service/sku/search', {
    params: data
  })
}
