<script>
import {
  getGoodItem,
  getGoodList,
  addGoodsMg,
  editGoodsItem,
  searchGoodsItem
} from '@/api/good'
export default {
  name: 'SkuManagement',
  data () {
    return {
      MyHead: {
        accept: 'application/json, text/plain',
        Authorization: this.$store.state.user.token
      },
      // fileList: [
      //   {
      //     name: 'food.jpeg',
      //     url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
      //   },
      //   {
      //     name: 'food2.jpeg',
      //     url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
      //   }
      // ],
      imageUrl: '',
      itemForm: {
        skuName: '',
        skuImage: '',
        price: 0,
        classId: '',
        unit: '',
        brandName: '',
        type: ''
      },
      editForm: {
        skuName: '',
        skuImage: '',
        price: 0,
        classId: '',
        unit: '',
        brandName: '',
        type: ''
      },
      rules: {
        skuName: [
          {
            required: true,
            message: '请输入商品名称',
            trigger: ['blur', 'change']
          }
        ],
        skuImage: [
          {
            required: true,
            message: '请输入商品品牌',
            trigger: ['blur', 'change']
          }
        ],
        price: [
          {
            required: true,
            message: '请输入商品名称',
            trigger: ['blur', 'change']
          }
        ],
        unit: [
          {
            required: true,
            message: '请输入商品类型',
            trigger: ['blur', 'change']
          }
        ],
        brandName: [
          {
            required: true,
            message: '请输入商品规格',
            trigger: ['blur', 'change']
          }
        ],
        type: [
          {
            required: true,
            message: '请上传商品图片',
            trigger: ['blur', 'change']
          }
        ]
      },
      num: 1,
      dialogVisible: false,
      dialogVisibleTwo: false,
      dialogVisibleThree: false,
      // 存放数据的数组
      itemList: [],
      // 当前页
      pageIndex: 1,
      // 每页显示的个数
      pageSize: 10,
      // 总数
      total: 0,
      // 存放商品类型的数组
      optionList: [],
      // 搜索的名字
      searchName: ''
    }
  },
  created () {
    this.getList()
    // console.log(this.$store.state.user.token)
  },
  methods: {
    // 跳转页面
    handleCurrentChange (val) {
      this.pageIndex = val
      // console.log(val)
      this.getList()
    },
    // 上传图片
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
      this.itemForm.skuImage = res
    },
    // 获取列表
    async getList () {
      const res = await getGoodItem({
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      })
      // console.log(res)
      res.currentPageRecords.forEach((item) => {
        item.price = item.price / 100
        item.createTime = item.createTime.replace('T', ' ')
      })
      this.itemList = res.currentPageRecords
      this.total = parseInt(res.totalCount)
    },
    // 获取商品类型
    async getItem () {
      this.dialogVisible = true
      const res = await getGoodList({
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      })
      // console.log(res)
      this.optionList = res.currentPageRecords
    },
    // 添加商品
    async addGoodItem () {
      this.$refs.form.validate()
      this.itemForm.classId = this.optionList.filter((item) => {
        return this.itemForm.type === item.className
      })[0].classId
      try {
        await addGoodsMg(this.itemForm)
      } catch (err) {
        console.log(err)
        this.$message.error(err.response.data)
      }
      this.$message.success('添加成功！')
      this.dialogVisible = false
      this.$refs.form.resetFields()
    },
    // 修改商品
    async editItem (obj) {
      this.dialogVisibleTwo = true
      this.editForm.skuName = obj.skuName
      this.editForm.skuImage = obj.skuImage
      this.editForm.price = obj.price
      this.editForm.classId = obj.classId
      this.editForm.unit = obj.unit
      this.editForm.brandName = obj.brandName
      // this.editForm.type = obj.type
      this.editForm.skuName = obj.skuName
      const res = await getGoodList({
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      })
      this.optionList = res.currentPageRecords
    },
    // 确认修改商品
    async editGoodItem () {
      this.$refs.editform.validate()
      try {
        await editGoodsItem(this.editForm, this.editForm.classId)
        // console.log(res1)
      } catch (e) {
        this.$message.error(e.response.data)
      }
      this.getList()
      this.dialogVisibleTwo = false
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    // 搜索商品
    async search () {
      const searchItem = {
        pageIndex: this.pageIndex,
        pageSize: this.pageSize,
        skuName: this.searchName
      }
      const res = await searchGoodsItem(searchItem)
      console.log(res)
      this.itemList = res.currentPageRecords
      this.pageIndex = parseInt(res.pageIndex)
      this.pageSize = parseInt(res.pageSize)
      this.totalPage = res.totalPage
      this.total = parseInt(res.totalCount)
      console.log(this.itemList)
    },
    // upload () {
    //   this.$refs.upload1.submit()
    //   this.dialogVisibleThree = false
    // },
    submitUpload () {
      this.$refs.upload.submit()
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    }
  }
}
</script>

